<template>
  <div class="container">
    <h1>Alcuni esempi query</h1>
    <hr />
    <div class="row">
      <div class="card" v-for="(item, index) in data" :key="index">
        <div class="card-header text-center">{{ item.title }}</div>
        <div class="card-body">
          <p class="card-text" style="white-space: pre-line">
            {{ item.code }}
          </p>
        </div>
        <div class="card-footer text-muted">creato: {{ item.created_at }}</div>
      </div>
    </div>
  </div>
</template>
<style scoped>
pre {
  background-color: rgb(32, 35, 36);
  width: 100%;
}
.card {
  width: 100%;
}
</style>
<script>
export default {
  data() {
    return {
      data: [],
    };
  },
  mounted() {
    this.submit();
  },
  methods: {
    submit() {
      var link = this.apiURL + "/varie/query";
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        console.log(r.data);
        this.data = r.data;

        this.$store.dispatch("loaded");
      });
    },
  },
};
</script>